import { followUser, unfollowUser } from "@/api/profile";
export default {
  name: "FollowAuthor",
  props: {
    author: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      isActing: false,
    };
  },
  methods: {
    async followUser(author) {
      this.isActing = true;
      let fn = null;
      if (author.following) {
        fn = unfollowUser;
      } else {
        fn = followUser;
      }
      const { data } = await fn(author.username);
      author.following = !author.following;
      this.isActing = false;
    },
  },
};
